@import '../common/styles/sass/variables.scss';
@import '../common/styles/sass/mixins.scss';

.infoBlocks {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;


  .infoBlocksContainer {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 80%;
    margin: 80px 0 80px 0;
  }
}



